export function isPolymerElementUnresolved(node) {
  const isCustomElement = node.tagName.indexOf('-') > -1 ;
  const isPolymer1 = node.__isPolymerInstance__ === true;
  const isPolymer2 = (Polymer.Element !== undefined && node instanceof Polymer.Element);

  return isCustomElement && !isPolymer1 && !isPolymer2;
}

export default {
  isPolymerElementUnresolved
};



