import CellsStorage from './storage';
import CellsConnector from '../connector';
import CellsSanitizer from '../sanitizer';
import eventManager from './events';

let CellsManagerPage = class CellsManagerPage {
  constructor(bridge) {
    this.storage = new CellsStorage({
      prefix: bridge.storagePrefix,
      persistent: bridge.cache
    });
    this.cache = bridge.cache;
    this.connector = new CellsConnector();
    this.sanitizer = new CellsSanitizer();
  }

  generateRequestUrl(page, options, config = {}) {
    let path = config.templates || 'componentsPages/';
    if (config.app) {
      path += config.app + '/';
    }
    return path + page + '.json';
  }

  /**
   * @param {Object} page
   */
  // eslint-disable-next-line no-unused-vars
  onPageDefinitionNotFound(page) {
    // Overwrite to make something when page definition is not found.
  }

  clear() {
    this.storage.clear();
  }

  get(page, options, config) {
    let spec = this.storage.getItem(page);
    if(this.cache && spec) {
      return (new Promise(
        function (resolve) {
          resolve(spec);
        }
      ));
    } else {
      return this._getFromConnector(page, options, config);
    }
  }

  _getFromConnector(page, options, config) {
    options = options || {};
    options.url = this.generateRequestUrl(page, options, config);

    eventManager.emit('page-request', options);

    let promise = this.connector.getJSON(options);

    return promise.then(this._onResponse.bind(this, page, options), this._onResponseFail.bind(this, page)).catch(error => error.message);
  }

  _onResponse(page, options, response) {
    response.page = page;

    eventManager.emit('page-response', response);

    let sanitizedData = this.sanitizer.parse(response);
    // sanitizedData = this.bridge.parse('data', sanitizedData);

    this.storage.setItem(page, sanitizedData);

    eventManager.emit('data-load', sanitizedData);

    return sanitizedData;
  }

  _onResponseFail(page) {
    if (this.onPageDefinitionNotFound) {
      this.onPageDefinitionNotFound(page);
    }

    return new Promise((resolve, reject) => reject(new Error(`Definition file for page '${page}' not found.`)));
  }
}

export default CellsManagerPage;



