import CellsBridgeComponent from 'cells-bridge/src/component';
import { isPolymerElementUnresolved } from './polymer-resolution-checker';

export default class PolymerComponent extends CellsBridgeComponent {

  isUnresolved() {
    return isPolymerElementUnresolved(this.node);
  }
}



