import CellsBridgeTemplate from 'cells-bridge/src/template';
import { isPolymerElementUnresolved } from './polymer-resolution-checker';

export default class PolymerTemplate extends CellsBridgeTemplate {

  isUnresolved() {
    return isPolymerElementUnresolved(this.node);
  }
}



