import CellsManagerTemplate from 'cells-bridge/src/manager/template';
import PolymerTemplate from './polymer-template';

export default class PolymerTemplateManager extends CellsManagerTemplate {

  _createCellsTemplate(spec, context) {
    return new PolymerTemplate(spec, context);
  }
}


