import CellsBridge from 'cells-bridge/src/bridge';
import PolymerComponentConnector from './polymer-component-connector';
import PolymerTemplateManager from './polymer-template-manager';
import PolymerComponent from './polymer-component';

export default class CellsPolymerBridge extends CellsBridge {

  constructor(config) {
    super(config);

    if (config.avoidPolymerEventCache === true) {
      var origGetEvent = Polymer.Base._getEvent;
      Polymer.Base._getEvent = function(type, bubbles, cancelable) {
        return origGetEvent(type, bubbles, cancelable, false);
      }
    }
  }

  _initDependencies(dependencies) {
    dependencies = Object.assign(
      {
        ComponentConnector: PolymerComponentConnector,
        TemplateManager: PolymerTemplateManager
      },
      dependencies
    );
    super._initDependencies(dependencies);
  }

  _createCellsComponent(spec, context) {
    return new PolymerComponent(spec, context);
  }
}



